.carousel {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  padding: 15px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  position: absolute;
  bottom: 30%;
  left: 50%;
  transform: translateX(-50%);
  transition: all ease-in-out 0.3s;

  &:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.3);
    transition: all ease-in-out 0.3s;
  }

  div {
    margin: 0 5px;
    width: 100px;
    height: 70px;
    border-radius: 10px;
    background-size: cover;
    border: 1px solid rgba(200, 200, 200, 0.3);
    transition: all ease-in-out 0.3s;
    opacity: 0.8;

    &:hover {
      z-index: 2;
      transform: scale(1.2) translateY(-20px);
      transition: all ease-in-out 0.3s;
      opacity: 1;
      cursor: pointer;
    }
  }
}